.wp-block-rise-blocks-image-slider{
	.slick-arrow {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        background: white;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        font-size: 25px;
        line-height: 1;
        z-index: 99;
        padding: 0;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            background-color: #FF8500;
        }

        &.slick-next  {
            right: 10px;
        }

        &.slick-prev  {
            left: 10px;
        }
    }

   

    ul.slick-dots {
        position: absolute;
        bottom: 30px;
        display: flex !important;
        list-style: none;
        left: 0;
        right: 0;
        margin: auto;
        justify-content: center;

        li {
            padding: 5px;
            button {
                border: none;
                background: #fff;
                height: 20px;
                width: 20px;
                border-radius: 50%;
                font-size: 0;
                cursor: pointer;
                padding: 0;
            }

            &.slick-active,
            &:hover {
                button {
                    background-color: #FF8500;
                }
            }
        }
    }

	.rise-blocks-image-slider {
		position: relative;
		> button {
			position: absolute;
			top: 10px;
			right: 10px;
			background: #fff;
			border: none;
			border-radius: 50%;
			height: 30px;
			width: 30px;
		}
	} 
}

button.rise-blocks-image-uploader-trigger {
    margin-bottom: 10px;
}